.nom-prenom-contenair{
    background-image: url("C:\Users\skynu\code\portfolio-mohamed-chikh\src\assets\Mangekyou-Sharingan.png");
    background-repeat: no-repeat;
    background-size: 140px;
    background-position: center;
    padding: 80px;
    margin-top: 3vh;
    width: 100px;
    margin: auto;
    animation: spin 8s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    transition: background-size 0.3s;
    
}
.nom-prenom-contenair:hover
{
    background-size: 130px;
}


.nom-prenom
{
  color: white;    
  font-size: 30px;
  text-shadow: 100px;
   position: relative; 
   top: 55px;
}

.sous-nom-prenom
{
    color: rgb(255, 255, 255);
    position: relative;
    top: 55px

}

.animated-text
{
    color: white;    
    font-size: 30px;
    text-shadow: 100px;
    
}

@keyframes spin
{
    from
    {
        transform: rotate(0deg);
    }
    to
    {
        transform: rotate(360deg);
    }
}