table {
    background: #454545;
    padding: 0vh;
    border-radius: 10px;
    border-spacing: 0px;
}

input, #result {
    background: #eaeaea;
    border: 0;
    height: 20px;
    padding: 2px 5px;
}

button {
    background-color: #eaeaea;
    border: 0;
    border-radius: 5px;
    height: 10vh;
    width: 10vh;
}