.contacts-contener.cacher.is-visible
{
    opacity: 1;
    animation: fadeInDown 1.2s ease-in-out forwards;
}



.contacts-contener.cacher
{
    color: white;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.464), rgb(28, 28, 28)) ;
    margin-left: 0vb;
    margin-right: 0vb;
    padding: 2vb;
    opacity: 0;
    float: right;
    float: left;
    width: 100%;
}
#name
{
    margin-left: 2vb;
    border-radius: 100px;
}

#email
{
    margin-left: 2vb;
    border-radius: 100px;
}

#message
{
    min-width: 30vb;
    max-width: 100vb;
    min-height: 5vb;
    max-height: 100vb;
    margin-top: 1vb;
    border-radius: 10px;
}

.name
{
    margin-left: -2.8vb;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.email
{
    margin-left: -1.9vb
}

.informations-contacts
{
    position: relative;
    top: -0vb;
    
}

button
{
    background-color: rgb(190, 0, 0);
    border-radius: 100px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding: 1vb;
    height: 4vb;
    color: aliceblue;
    transition: all 300ms;
}

button:hover
{
    color: black;
    background-color: white;
    transform: translateY(-0.3vb);
    transition: all 300ms;
}