.competences-contener.cacher.is-visible
{
    opacity: 1;
}

.competences-contener.cacher
{
    background-image: linear-gradient(to left, rgb(212, 0, 64), rgb(28, 28, 28));
    opacity: 0;
   
    padding: 0vh;
    margin-top: 5vb;
    animation: fadeInRight 1.2s ease-in-out forwards;
    width: 70%;
    height: 00%;
    
    float: right;
}
.competences
{
    color: rgb(255, 255, 255);
    background-color: rgba(255, 255, 255, 0);
    width: 100%;
    height: 50%;
    float: left;
}

.langues
{
    color: rgb(255, 255, 255);
    background-color: rgba(255, 255, 255, 0);
    min-width: 40%;
}

.rond-langues
{
    margin-left: 5px;
    margin-right: 13px;
    position: relative;
    top: 10px;
    filter: grayscale(100%) brightness(800%);
}
.text-francais
{
    margin-right: 1.4vh;
}
.text-anglais
{
    margin: 1vh;
}
table
{
    margin: auto;
    background-color: black;
    min-width: 100%;
}
td
{
    padding: 2vw;
    filter: grayscale(100%) brightness(800%);
    min-width: 80%;
}



.competences-img
{
    position: absolute;
    top: 135px;
    left: -100%;
    width: 16vw;
    background-image: linear-gradient(to top left, rgb(255, 208, 208), rgb(255, 238, 238));
    border-radius: 50%;
    border: 0.5vw solid rgb(175, 0, 0);
    animation: fadeInLeft 1s ease-in-out backwards;
    animation-delay: 1s;
}

