.diplomes-contener.cacher.is-visible
{
    opacity: 1;
    animation: fadeInLeft 1.2s ease-in-out forwards;
}

.diplomes-contener.cacher
{
    background-image: linear-gradient(to right, rgb(212, 0, 64), rgb(28, 28, 28));
    color: rgb(255, 255, 255);
    margin-top: 5vh;
    margin-left: 0vh;
    margin-right: 40vh;
    padding-top: 2vh;
    padding-bottom: 2vh;
    opacity: 0;
    min-width: 75vw;
    position: relative;
    float: left;
    clear: both;
   }

.diplome
{
    background-image: linear-gradient(to right, rgb(28, 28, 28), rgba(0, 0, 0, 0)) ;
    margin: 2vh;
    padding: 3vh;
}