body
{
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow-x: hidden;
}

.App {
  margin: 0;
  text-align: center;
  background-color: rgb(28, 28, 28);
  width: 100vw;
  overflow-x: hidden;
  height: 10000%;
}

@font-face {
  font-family: 'OpenSans-Regular';
  src: url('fonts/OpenSans-Regular.ttf') format('tff');
  font-weight: normal;
  font-style: normal;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 100s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#babinks
{
  background-color: black;
  padding: 80px;
  padding-top: 100px;
  margin-top: 100px;
  margin-left: 10px;
  margin-right: 10px;
}

.babinks-titre
{
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.rondPlein
{
  width: 3vh;
  height: 3vh;
  margin: 0.5vh;
}
.rondVide
{
  width: 3vh;
  height: 3vh;
  margin: 0.5vh;
}

@keyframes fadeInLeft {
  from { opacity: 0; transform: translateX(-100%); filter: blur(20px);}
  to { opacity: 1;}
}

@keyframes fadeInRight {
  from { opacity: 0; transform: translateX(100%); filter: blur(20px);}
  to { opacity: 1;}
}

@keyframes fadeInDown {
  from { opacity: 0; transform: translateY(100%);}
  to { opacity: 1;}
}

