.projets-contener.cacher.is-visible
{
    opacity: 1;
    animation: fadeInRight 1.2s ease-in-out forwards;
}

.projets-contener.cacher
{
    color: white;
    background-image: linear-gradient(to left, rgb(212, 0, 64), rgb(28, 28, 28));
    margin-top: 5vh;
    margin-left: 40vh;
    margin-right: 0vh;
    opacity: 0;
    margin-bottom: 5vb;
    left: 50%;
    float: right;
    clear: both;
}

#projets
{
    display: flex;
    flex-wrap: wrap-reverse;
    left: 50%;
    gap: 8px;
    max-width: 916px;
    width: calc(100% -20px);
    padding-bottom: 2vb;
    margin: auto;
}

.projet 
{
    background-color: rgba(255, 255, 255, 0.02);
    border: 1px solid rgba(255, 0, 0, 0.1);
    border-radius: 10px;
    cursor: pointer;
    height: 260px;
    position: relative;
    width: 300px;
    margin: auto;
    margin-bottom: 2vb;
    
}

.projet:hover::before
{
    opacity: 1;
}

.projet::before
{
    background: radial-gradient(
        800px circle at var(--mouse-x) var(--mouse-y),
        rgba(255, 255, 255, 0.06),
        transparent 40%
    );
    border-radius: inherit;
    content: "";
    height: 100%;
    left: 0px;
    opacity: 1;
    position: absolute;
    top: 0px;
    transition: opacity 500ms;
    width: 100%;
    z-index: 2;
}

.itachi
{
  border-radius: inherit;
  align-items: center;
  width: 100%; /* Assurer que l'image ne dépasse pas la largeur de la div */
  height: 100%;
  
  transform: translateY(0px);
  object-fit: cover;
  object-position: 0 -21vb;
}

.ritchie
{
    height: 100%;
    width: 80vb;
    object-fit: cover;
    object-position: -40vb;
    border-radius: inherit;
    max-height: 100%;
}