

#presentation
{
    opacity: 0;
    transform: translateY(-6%);
    padding: 5vb;
    margin-top: 18vb;
    margin-left: 0vh;
    margin-right: 40vh;
    background-image: linear-gradient(to right, rgb(212, 0, 64), rgb(28, 28, 28));
    background-clip: padding-box;
    text-align: center;
    height: 80%;
    width: 70%;
    font-size: calc(12px + 2vw);
    color: rgb(255, 255, 255);
    transition: opacity 1s;
    opacity: 1;
    animation: fadeInLeft 1.2s ease-in-out;
}

.linkedin-img
{
    width: 7vw;
    padding: 0.5vh;
    transition: 0.5s;
}

.linkedin-img:hover
{
    width: 7.5vw;
    filter: brightness(150%);
    transition: 1s;
}

.circle1
{
    background-image: linear-gradient(to top, rgb(255, 255, 255), rgb(248, 58, 74));
    position: absolute;
    top: 39%;
    left: 80%;
    color: rgba(240, 248, 255, 0);
    border-radius: 50%;
    width: 15vw;
    height: 15vw;
    overflow: hidden;
    animation:  rotate infinite 1s linear,fadeInRight 1.2s ease-in-out backwards;
    animation-delay: 500ms;
}

.circle2
{
    background-color: rgb(28, 28, 28);
    position: absolute;
    color: rgb(28, 28, 28);
    left: 1.5vw;
    top: 1.5vw;
    border-radius: 50%;
    width: 12vw;
    height: 12vw;
    animation: fadeInRight 1.2s ease-in-out backwards;
    animation-delay: 500ms;
}

@keyframes rotate
{
    from { transform: rotate(0);}
    to { transform: rotate(360deg);}
}

